.SearchBar {
  position: fixed;
  height: 80px;
  padding: 20px;
  width: 100%;
  left: 0;
  top: 0;
  background-color: black;
}

.SearchBar input[type='text'] {
  background-color: transparent;
  border: 1px solid gray;
  width: 100%;
  height: 40px;
  padding-left: 1.4em;
  font-size: 1.2em;
  color: white;
}


